.inputBox, .inputBox:focus-visible,.inputBox:active {
    align-self: flex-end;
    /* width: fit-content; */
    line-height: 1.6rem;
    /* border-radius: 5px; */
    padding: 6px;
    padding-right: 32px;
    color: #7f7f80;
    text-align: end;
    font-size: 1.2rem;
    font-family: cursive;
    border: none;
    border-bottom: 2px #4DB3E4 solid;
    background: transparent;
    outline-width: 0;
    min-width: 14ch;
}

svg{
    color:#4DB3E4 !important;
    margin-left: -24px;
}