.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.container {
  /* margin: 0px 30px 0px 30px;
  padding: 10px 30px 0px 30px; */
  background: #F4E683;
  border-radius: 3px;
  /* height: 100%;
  display: flex;
  flex-direction: column; */
  
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.top-nav{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  padding-bottom: 20px;
}

.nav-header{
  display: inline-flex;
  align-items: center;
  color: #B83C70 !important;
}

.nav-header svg{
  font-size: 3em;
    color: #B83C70 !important;
}

.nav-header h2{
  letter-spacing: 4px;
}