.img-body {
    /* Mobile first */
    display: flex;
    flex-direction: column;
    gap: 10px;
    justify-content: center;
}

.img-body .column {
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.img-content img {
    width: 100%;
    border-radius: 5px;
    height: 200px;
    object-fit: cover;
}
.column .img-selected{
    /* max-width: 80% !important;
    height: 100px; */
}
.column .img-content-text{
    display: none !important;
}
.unselected{
    flex-direction: row !important;
    /* width: 50% !important */
    overflow-y: overlay;
    max-height: 87vh;
}
::-webkit-scrollbar {
    background: #fff;
    width: 5px !important;
    color:black;
    background-color: #F4E683;
}
::-webkit-scrollbar-thumb {
    background: #B83C70;
    border-radius: 10px;
}
.unselected img{
    height: auto;
}
.column-selected img{
    width: auto;
    height: auto;
    object-fit: cover;
}
.column-selected .img-selected{
    justify-content: center;
    display: inline-block;
}
.column-selected{
    width: 100% !important;
    position: relative;
    display: inline-block;
    padding-bottom: 1.125em;
}


.img-content {
    position: relative;
    cursor: pointer;
}

@media only screen and (min-width: 768px) {
    .img-body {
        flex-direction: row;
    }
}

.img-content {
    margin-right: 10px
}

.img-content-text {
    justify-content: space-between !important;
    flex-direction: column;
    align-items: baseline;
    /* padding: 5px; */
    padding-top: 5px;
    background-color: antiquewhite;
    width: auto;
    /* margin-left: 5px; */
    padding-left: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    /* white-space: nowrap; */
    text-align: left;
    position: absolute !important;
    left: 0;
    right: 0;
}

.img-content-text div {
    padding: 4px
}

.img-content-text div h4 {
    margin: 0;
}